import React from 'react'
import PropTypes from 'prop-types'
import Tags from '../../tags'
import { connect } from 'react-redux'

import { addTask } from '../actions'

class NewTask extends React.Component {

	constructor(props) {
		super(props)

		this.state = {
			tags:[],
		}

		this.handleChange = this.handleChange.bind(this)
		this.handleSubmit = this.handleSubmit.bind(this)
		this.addTag = this.addTag.bind(this)
		this.isOn
	}



	handleSubmit(e) {
		e.preventDefault()

		//Dispatch action!!!!

		this.props.save({
			title: this.state.title,
			goal : this.state.goal,
			tags : this.state.tags,
		})

		this.setState({
			title:'',
			goal:'',
			tags:[],
		})
	}

	isOn(e){
		e.preventDefault()
		e.isTagOn = 'tagIsOn'
	}

	addTag(event){


		event.preventDefault()
		let tags = this.state.tags
		tags.push( event.target.name )

		this.setState( {
			tags: tags,
		} )
	}



	handleChange(event) {
		this.setState({
			[event.target.name]: event.target.value
		})
	}


	render() {
		// let isTagOn = ''

		return (
      <form onSubmit={this.handleSubmit}>
				<div id="{menuClass}">
				<h5>Tags</h5>
					<Tags />
				</div>
				<label>
          Task:
          <input placeholder="Task Title" name="title" type="text" onChange={this.handleChange} value={this.state.title}  />
        </label>
        <label>
          Task Objective:
          <input placeholder = "Task objective" type="text" name = "goal" onChange={this.handleChange} value={this.state.goal} />
        </label>
        <button id="submitBtn" type="submit" value="Submit" onChange={this.handleSubmit}>submit</button>
      </form>
		)
	}
}
// <button className ={isTagOn} name="Urgent" value="Urgent" onClick={this.addTag,this.isOn}>Urgent </button>
// <button name="Design" value="Design" onClick={this.addTag,this.isOn}>Design</button>
// <button name="Add Functionality" value="Add functionality" onClick={this.addTag,this.isOn}>Add Functionality</button>
// <button name="Internal" value="Internal" onClick={this.addTag,this.isOn}>Internal</button>

NewTask.propTypes = {
	save: PropTypes.func.isRequired,
}

export default connect(
	state => ({
		tags: state.tags
	}), {
		addTask
	}
)(NewTask)



